import React from "react";

const ResolutionConf = ({ width, height, onResolutionChange }) => (
  <div>
    <h2>Nastaven rozlien pltna</h2>
    <label>
      ka:<input
        type="number"
        value={width}
        onChange={event => {
          onResolutionChange({ width: event.target.value });
        }}
      />px
    </label>
    <br />

    <label>
      Vka:<input
        type="number"
        value={height}
        onChange={event => {
          onResolutionChange({ height: event.target.value });
        }}
      />px
    </label>
    <p>
      !!! Zmna rozlien pekresl pltno. Vysok rozlien si nrokuje velk
      vkon !!!
    </p>
  </div>
);

export default ResolutionConf;



