import React from "react";
import StrokeAndFillConf from "./StrokeAndFillConf";

const RowsConf = ({ widthC, heightC, nice, lineWidth, fill, onChange }) => (
  <div>
    <h3>Nastaven dnch koeficient</h3>
    <label>
      Nastaven koeficinet ky ({widthC}): <br />
      <input
        type="range"
        value={widthC}
        min="0"
        max="250"
        step="1"
        onChange={event => {
          onChange("widthC", event.target.value);
        }}
      />
    </label>
    <br />
    <label>
      Nastaven koeficinet vky ({heightC}): <br />
      <input
        type="range"
        value={heightC}
        min="0"
        max="250"
        step="1"
        onChange={event => {
          onChange("heightC", event.target.value);
        }}
      />
    </label>
    <br />
    <label>
      Uhlazen reim:
      <input
        type="checkbox"
        checked={nice}
        onChange={event => {
          onChange("nice", event.target.checked);
        }}
      />
    </label>
    <StrokeAndFillConf fill={fill} lineWidth={lineWidth} onChange={onChange} />
  </div>
);

export default RowsConf;



